@font-face {
  font-family: "Poppins";
  font-weight: 400;
  src: url("./../fonts/poppins-regular.woff2") format("woff2"), url("./../fonts/poppins-regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Poppins";
  font-weight: 500;
  src: url("./../fonts/poppins-medium.woff2") format("woff2"), url("./../fonts/poppins-medium.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Poppins";
  font-weight: 700;
  src: url("./../fonts/poppins-bold.woff2") format("woff2"), url("./../fonts/poppins-bold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Cormorant Garamond";
  font-weight: 400;
  src: url("./../fonts/cormorantgaramond-regular.woff2") format("woff2"), url("./../fonts/cormorantgaramond-regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Cormorant Garamond";
  font-weight: 600;
  src: url("./../fonts/cormorantgaramond-semibold.woff2") format("woff2"), url("./../fonts/cormorantgaramond-semibold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Cormorant Garamond";
  font-weight: 700;
  src: url("./../fonts/cormorantgaramond-bold.woff2") format("woff2"), url("./../fonts/cormorantgaramond-bold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Cormorant Garamond";
  font-style: italic;
  src: url("./../fonts/cormorantgaramond-italic.woff2") format("woff2"), url("./../fonts/cormorantgaramond-italic.woff") format("woff");
  font-display: swap; }

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  scroll-behavior: smooth; }

body {
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  font-family: "Poppins", "Courier New", "Courier", monospace; }

*,
*::after,
*::before {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

h2 {
  margin: 0;
  font-weight: 700;
  font-size: 34px;
  line-height: 130%;
  color: #1d1613; }

p {
  margin: 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  color: #1d1613; }

.page-header__search input {
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  font-family: "Poppins", "Courier New", "Courier", monospace; }

.header__buttons button {
  padding: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  background-color: inherit;
  color: #1d1613;
  border: 0; }

.main-nav ul {
  padding: 0;
  margin: 0;
  list-style: none; }

.main-nav a {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #1d1613;
  text-decoration: none; }

.promo {
  position: relative; }

.promo__title {
  position: absolute;
  top: 0; }

.promo h2 {
  margin: 0;
  font-weight: 700;
  font-size: 45px;
  line-height: 130%;
  color: #ffffff; }

.promo p {
  margin: 0;
  font-style: italic;
  font-size: 38px;
  line-height: 130%;
  color: #ffffff; }

.new-jewelery__title a {
  text-decoration: none;
  border-bottom: 1px solid #a87b62; }

.new-jewelery__catalog ul {
  padding: 0;
  margin: 0;
  list-style: none; }

.new-jewelery__catalog a {
  display: block;
  text-decoration: none; }

.new-jewelery__catalog h3 {
  margin: 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  color: #1d1613; }

.new-jewelery__catalog p {
  font-weight: 700;
  font-size: 14px;
  line-height: 130%;
  color: #1d1613; }

.breadcrumbs__button {
  padding: 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  background-color: inherit;
  color: #9b9997;
  border: none; }
  .breadcrumbs__button--active {
    color: #1d1613; }

.blog a {
  display: inline-block;
  text-decoration: none;
  border-bottom: 1px solid #a87b62; }

.blog__article {
  background-color: #f0eae4; }

.blog__article span {
  font-size: 14px;
  color: #9b9997; }

.blog__article p:nth-child(2n) {
  font-family: "Cormorant Garamond", "Times New Roman", "Times", serif;
  color: #000000;
  font-weight: 600;
  font-size: 26px;
  line-height: 130%; }

.blog__article p:last-child {
  font-family: "Poppins","Courier New", "Courier", monospace;
  font-weight: 500;
  font-size: 16px;
  line-height: 130%; }

.questions ul {
  padding: 0;
  margin: 0;
  list-style: none; }

.questions h3 {
  margin: 0;
  font-family: "Cormorant Garamond", "Times New Roman", "Times", serif;
  font-weight: 700;
  font-size: 34px;
  line-height: 130%;
  letter-spacing: 0.03em;
  color: #1d1613; }

.questions button {
  padding: 0;
  font-family: "Poppins", "Courier New", "Courier", monospace;
  background-color: inherit;
  border: none; }

.questions span {
  font-weight: 500;
  font-size: 18px;
  line-height: 130%;
  color: #1d1613; }

.questions p {
  font-size: 16px;
  line-height: 130%; }

.follow h2 {
  font-family: "Cormorant Garamond", "Times New Roman", "Times", serif;
  letter-spacing: 0.03em; }

.follow a {
  display: inline-block;
  text-decoration: none;
  border-bottom: 1px solid #a87b62; }
